@Composable
fun WorksPage() {
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier
            .id("works")
            .fillMaxWidth()
            .padding(top = 5.percent),
    ) {
        HeaderSection(
            title = "Works",
            modifier = sectionModifier,
        )
        Div(
            attrs = sectionModifier
                .then(other = WorksStyles.GridStyle.toModifier())
                .fillMaxSize()
                .toAttrs(),
        ) {
            listOf(
                Work(
                    imageSrc = "img_works_1.jpg",
                    title = "地域ブランド再構築",
                    subtitle = "三重県・松阪牛 × Cazacle Tour",
                    body = "「わざわざ三重に来てもらう理由は？」という問いに対し、\n" +
                        "松阪牛を“旅の目的”となる体験価値として再構築。\n" +
                        " YamaTripsは、“シャンプーを浴びる清潔な牛”というユニークな切り口を開発し、インバウンド向けに発信。\n" +
                        " SNSで1,000万以上にリーチし、三重を目的地として印象づけることに成功。現地ツアーや宿泊・飲食施設への訪日客からの問い合わせが継続的に増加しています。",
                ),
                Work(
                    imageSrc = "img_works_2.jpg",
                    title = "観光課題の特定と解決",
                    subtitle = "三重県・紀北町 × 熊野古道",
                    body = "なぜ、和歌山側の熊野古道には人が集まり、三重側には来ないのか？\n" +
                        " この問いに対し、YamaTripsは30万人以上の海外フォロワーへのSNS調査と、インフルエンサー3名による現地体験発信を実施。\n" +
                        "「何をどう体験したいのか」「どこに不便を感じるのか」について観光客視点で定量・定性の両面から課題を分析。\n" +
                        " その結果をもとに、自治体や事業者へ具体的な改善提案を行い、\n" +
                        "観光まちづくりの再起動を後押ししました。\n" +
                        "“本当に行きたくなる町”へ。海外目線を起点とした第一歩を支援しました。",
                ),
                Work(
                    imageSrc = "img_works_3.jpg",
                    title = "専門的視点でのツーリズム改善提案",
                    subtitle = "山梨県・中枢エリア",
                    body = "訪日客で賑わう富士五湖エリアとは対照的に、観光の流れが停滞する山梨県中部。\n" +
                        " YamaTripsはインバウンド戦略の専門家として現地を訪問し、東京からのアクセス動線、旅行者の期待値、情報流通の構造などを多角的に分析しました。\n" +
                        "現地関係者と連携し、“旅の動線”をゼロから再設計。さらにSNS上で仮説を検証し、実際の反応をもとに戦略へ反映しました。\n" +
                        " 現場とデジタルを往復するアプローチで、地域への人の流れを創出する足がかりを築いています。",
                ),
                Work(
                    imageSrc = "img_works_4.jpg",
                    title = "「災害大国・日本」ならではの、“安心”を観光資源として活用するプロモーション戦略",
                    subtitle = "東京都・防災施設",
                    body = "外国人旅行者の不安を払拭するため、防災施設を舞台にしたリール動画を制作。\n" +
                        "専門家の解説と施設の構造美を織り交ぜ、都市の“安全性”を学びある観光体験として発信しました。動画は20万回以上のリーチと高いエンゲージメントを獲得し、東京の“安心して訪れられる都市”としての新たな価値を広く伝えました。",
                ),
                Work(
                    imageSrc = "img_works_5.jpg",
                    title = "知られざるアクティブ体験を可視化",
                    subtitle = "鹿児島・フェアフィールド・バイ・マリオット",
                    body = "手つかずの自然が残る鹿児島を舞台に、宿泊施設を自然体験の拠点としてブランディング。\n" +
                        " 道の駅に隣接したホテルの魅力を引き出す動画プロモーションを展開し、ドローン映像やアクティビティの臨場感を通じて“体験価値”を可視化しました。\n" +
                        " 動画は100万回以上の再生を達成し、滞在先としての新たな価値訴求に成功しました。",
                ),
                Work(
                    imageSrc = "img_works_6.jpg",
                    title = "国内仕様からインバウンド仕様へのアップデート支援",
                    subtitle = "北海道・東川町",
                    body = "冬季の認知不足や、宿泊施設の国内向け仕様による訪日客の取りこぼしといった課題に対し、YamaTripsが改善支援を実施。\n" +
                        " 海外旅行者の視点で宿の課題を分析し、導線設計・情報整備・設備提案まで実装ベースで伴走。\n" +
                        " SNS運用ではわずか3ヶ月で1,500フォロワーを突破し、着実にインバウンド送客への基盤を構築しています。",
                ),
            ).forEachIndexed { index, work ->
                WorksItem(
                    imageSrc = work.imageSrc,
                    title = work.title,
                    subtitle = work.subtitle,
                    body = work.body,
                    // modifier = Modifier.gridItem(row = index / 3 + 1, column = index % 3 + 1),
                )
            }
        }
    }
}

object WorksStyles {
    val GridStyle = CssStyle {
        base {
            Modifier
                .gap(48.px)
                .padding(48.px)
                .grid {
                    rows { repeat(6) { size(2.fr) } }
                    columns { repeat(1) { size(2.fr) } }
                }
        }
        md {
            Modifier
                .gap(48.px)
                .padding(48.px)
                .grid {
                    rows { repeat(3) { size(2.fr) } }
                    columns { repeat(2) { size(2.fr) } }
                }
        }
        lg {
            Modifier
                .gap(48.px)
                .padding(48.px)
                .grid {
                    rows { repeat(2) { size(2.fr) } }
                    columns { repeat(3) { size(2.fr) } }
                }
        }
    }
}
