@Composable
fun WorksItem(
    imageSrc: String,
    title: String,
    subtitle: String,
    body: String,
    modifier: Modifier = Modifier,
) {
    var isActive by remember { mutableStateOf(false) }

    Column(
        verticalArrangement = Arrangement.spacedBy(space = 12.px),
        modifier = modifier
            .padding(12.px)
            .then(
                other = animationModifier(
                    isActive = isActive,
                    animation = WorksItemStyles.slideInFromBottom.toAnimation(
                        duration = 800.ms,
                        timingFunction = AnimationTimingFunction.EaseOut,
                    ),
                ),
            ),
        ref = intersectionObserver { isActive = true },
    ) {
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .aspectRatio(ratio = 1f)
                .position(position = Position.Relative),
        ) {
            Image(
                src = imageSrc,
                modifier = Modifier
                    .fillMaxSize()
                    .objectFit(objectFit = ObjectFit.Cover)
                    .position(Position.Absolute),
            )
            Box(
                modifier = Modifier
                    .fillMaxSize()
                    .position(Position.Absolute),
            )
        }
        SpanText(
            text = title,
            modifier = AppTheme.typography.title1.toModifier()
                .textAlign(textAlign = TextAlign.Center)
                .whiteSpace(whiteSpace = WhiteSpace.PreLine)
                .fillMaxWidth(),
        )
        SpanText(
            text = subtitle,
            modifier = AppTheme.typography.body1.toModifier()
                .textAlign(textAlign = TextAlign.Center)
                .whiteSpace(whiteSpace = WhiteSpace.PreLine)
                .fillMaxWidth(),
        )
        SpanText(
            text = body,
            modifier = AppTheme.typography.body2.toModifier()
                .whiteSpace(whiteSpace = WhiteSpace.PreLine)
                .fillMaxWidth(),
        )
    }
}

object WorksItemStyles {
    val slideInFromBottom = Keyframes {
        from {
            Modifier
                .opacity(0)
                .translateY(ty = 40.percent)
        }
        to {
            Modifier
                .opacity(1)
        }
    }
}

