package com.yamatrips.homepage.core.styles

import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.silk.theme.colors.ColorMode

class LocalAppColors(
    val background: Color,
    val cobweb: Color,
    val onBackground: Color,
)

object AppColors {
    val light = LocalAppColors(
        background = Color.rgb(value = 0x000000),
        cobweb = Color.rgb(value = 0x8DD2DD),
        onBackground = Color.rgb(value = 0xFFFFFF),
    )
    val dark = LocalAppColors(
        background = Color.rgb(value = 0x000000),
        cobweb = Color.rgb(value = 0x8DD2DD),
        onBackground = Color.rgb(value = 0xFFFFFF),
    )
}

val ColorMode.appColors
    get() = when (this) {
        ColorMode.LIGHT -> AppColors.light
        ColorMode.DARK -> AppColors.dark
    }
