@Composable
fun MessageSection(
    title: String,
    body: String,
    imageSrc: String,
    modifier: Modifier = Modifier,
) {
    var isActive by remember { mutableStateOf(false) }

    Column(
        verticalArrangement = Arrangement.spacedBy(space = 40.px),
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = modifier
            .fillMaxWidth()
            .then(
                other = animationModifier(
                    isActive = isActive,
                    animation = WorksItemStyles.slideInFromBottom.toAnimation(
                        duration = 800.ms,
                        timingFunction = AnimationTimingFunction.EaseOut,
                    ),
                ),
            ),
        ref = intersectionObserver { isActive = true },
    ) {
        SpanText(
            text = title,
            modifier = AppTheme.typography.header3.toModifier()
                .textAlign(textAlign = TextAlign.Center),
        )
        Row(
            horizontalArrangement = Arrangement.SpaceEvenly,
            verticalAlignment = Alignment.CenterVertically,
        ) {
            Image(
                src = imageSrc,
                modifier = Modifier
                    .width(size = 30.percent)
                    .aspectRatio(ratio = 1f)
                    .objectFit(objectFit = ObjectFit.Cover)
                    .clip(shape = CircleF()),
            )
            SpanText(
                text = body,
                modifier = AppTheme.typography.body1.toModifier()
                    .width(size = 50.percent)
                    .whiteSpace(whiteSpace = WhiteSpace.PreLine),
            )
        }
    }
}

object MessageStyles {
    val slideInFromBottomKeyframes = Keyframes {
        from {
            Modifier
                .opacity(0)
                .translateY(ty = 40.percent)
        }
        to {
            Modifier
                .opacity(1)
        }
    }
}

