package com.yamatrips.homepage.core.styles

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.silk.style.CssStyleBaseScope
import com.varabyte.kobweb.silk.style.CssStyleScope
import com.varabyte.kobweb.silk.theme.colors.ColorMode

data class LocalAppTheme(
    val colors: LocalAppColors,
    val typography: AppTypography,
    val variants: AppVariants,
)

val AppTheme: LocalAppTheme
    @Composable get() = LocalAppTheme(
        colors = ColorMode.current.appColors,
        typography = AppTypography,
        variants = AppVariants,
    )

fun AppTheme(colorMode: ColorMode) = LocalAppTheme(
    colors = colorMode.appColors,
    typography = AppTypography,
    variants = AppVariants,
)

val CssStyleBaseScope.AppTheme
    get() = AppTheme(colorMode = colorMode)

val CssStyleScope.AppTheme
    get() = AppTheme(colorMode = colorMode)
