package com.yamatrips.homepage.core.components

import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import com.varabyte.kobweb.compose.css.WhiteSpace
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.borderRight
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.opacity
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.modifiers.translateX
import com.varabyte.kobweb.compose.ui.modifiers.whiteSpace
import com.varabyte.kobweb.compose.ui.modifiers.width
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.style.animation.Keyframes
import com.varabyte.kobweb.silk.style.animation.toAnimation
import com.varabyte.kobweb.silk.style.toModifier
import com.yamatrips.homepage.core.ext.animationModifier
import com.yamatrips.homepage.core.ext.intersectionObserver
import com.yamatrips.homepage.core.styles.AppTheme
import org.jetbrains.compose.web.css.AnimationTimingFunction
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

@Composable
fun ContentSection(
    title: String,
    body: String,
    header: String,
    index: Int? = null,
    subtitle: String? = null,
    subheader: String? = null,
    modifier: Modifier = Modifier,
) {
    var isActive by remember { mutableStateOf(false) }

    Row(
        verticalAlignment = Alignment.Top,
        modifier = modifier.fillMaxWidth(),
        ref = intersectionObserver { isActive = true },
    ) {
        Row(
            verticalAlignment = Alignment.Top,
            modifier = Modifier
                .width(size = 45.percent)
                .then(other = animationModifier(isActive = isActive, animation = ContentStyles.slideInFromLeftKeyframes.toSlideAnimation())),
        ) {
            val borderColor = AppTheme.colors.onBackground
            Column(
                modifier = Modifier
                    .width(size = 70.percent)
                    .padding(right = 24.px)
                    .borderRight {
                        width(width = 4.px)
                        color(color = borderColor)
                        style(lineStyle = LineStyle.Solid)
                    },
            ) {
                Row(
                    horizontalArrangement = Arrangement.spacedBy(space = 8.px),
                ) {
                    if (index != null) {
                        SpanText(
                            text = index.toString().padStart(length = 2, padChar = '0'),
                            modifier = AppTheme.typography.title2.toModifier(),
                        )
                    }
                    if (subtitle != null) {
                        SpanText(
                            text = subtitle,
                            modifier = AppTheme.typography.title2.toModifier(),
                        )
                    }
                }
                SpanText(
                    text = title,
                    modifier = AppTheme.typography.header3.toModifier()
                        .fillMaxWidth(),
                )
            }
        }

        ContentSectionText(
            header = header,
            subheader = subheader,
            body = body,
            modifier = Modifier
                .width(size = 55.percent)
                .then(other = animationModifier(isActive = isActive, animation = ContentStyles.slideInFromRightKeyframes.toSlideAnimation())),
        )
    }
}

@Composable
fun ContentSectionText(
    header: String? = null,
    subheader: String? = null,
    body: String? = null,
    modifier: Modifier = Modifier,
) {
    Column(
        verticalArrangement = Arrangement.spacedBy(space = 12.px),
        horizontalAlignment = Alignment.Start,
        modifier = modifier,
    ) {
        if (header != null) {
            SpanText(
                text = header,
                modifier = AppTheme.typography.title1.toModifier(),
            )
        }
        if (subheader != null) {
            SpanText(
                text = subheader,
                modifier = AppTheme.typography.body1.toModifier()
                    .whiteSpace(whiteSpace = WhiteSpace.PreLine)
                    .padding(bottom = 12.px),
            )
        }
        if (body != null) {
            SpanText(
                text = body,
                modifier = AppTheme.typography.body2.toModifier()
                    .fillMaxWidth()
                    .whiteSpace(whiteSpace = WhiteSpace.PreLine),
            )
        }
    }
}

@Composable
private fun Keyframes.toSlideAnimation() = toAnimation(
    duration = 800.ms,
    timingFunction = AnimationTimingFunction.EaseOut,
)

object ContentStyles {
    val slideInFromLeftKeyframes = Keyframes {
        from {
            Modifier
                .opacity(0)
                .translateX(tx = (-30).percent)
        }
        to {
            Modifier
                .opacity(1)
        }
    }

    val slideInFromRightKeyframes = Keyframes {
        from {
            Modifier
                .opacity(0)
                .translateX(tx = 30.percent)
        }
        to {
            Modifier
                .opacity(1)
        }
    }
}
