import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.fr
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.vh

class PageLayoutData(val title: String)

@Composable
@Layout
fun PageLayout(ctx: PageContext, content: @Composable BoxScope.() -> Unit) {
    val data = ctx.data.getValue<PageLayoutData>()
    var isActive by remember { mutableStateOf(false) }
    var isScrolled by remember { mutableStateOf(value = false) }
    var backgroundColor by remember { mutableStateOf<Color>(Colors.Transparent) }
    val targetBackgroundColor = PageLayoutStyle.headerBackgroundColor(colorMode = ColorMode.current)
    val isScrollAnimationModifier = Modifier
        .onAnimationStart {
            backgroundColor = Colors.Transparent
            isActive = true
        }
        .animation(
            PageLayoutStyle.headerFadeInKeyframes.toAnimation(
                duration = 500.ms,
                timingFunction = AnimationTimingFunction.EaseIn,
            ),
        )
        .onAnimationEnd {
            backgroundColor = targetBackgroundColor
        }

    val isTopAnimationModifier = Modifier
        .onAnimationStart {
            backgroundColor = Colors.Transparent
        }
        .animation(
            PageLayoutStyle.headerFadeOutKeyframes.toAnimation(
                duration = 500.ms,
                timingFunction = AnimationTimingFunction.EaseIn,
            ),
        )

    LaunchedEffect(data.title) {
        document.title = "YamaTrips ${data.title}"
    }

    Box(
        Modifier
            .fillMaxWidth()
            .minHeight(100.vh)
            .background(color = AppTheme.colors.background)
            .gridTemplateRows {
                size(1.fr)
                size(minContent)
            },
        contentAlignment = Alignment.Center,
    ) {
        Box(
            contentAlignment = Alignment.TopCenter,
            modifier = Modifier.fillMaxSize().gridRow(1),
        ) {
            content()
            NavHeader(
                modifier = Modifier
                    .position(Position.Fixed)
                    .then(
                        other = if (isScrolled) {
                            isScrollAnimationModifier
                        } else if (isActive) {
                            isTopAnimationModifier
                        } else {
                            Modifier
                        },
                    )
                    .background(backgroundColor),
                ref = ref { element ->
                    document.addEventListener(
                        type = "scroll",
                        callback = {
                            val scrollOffset = document.scrollingElement?.scrollTop
                            if (scrollOffset != null) {
                                isScrolled = scrollOffset > 10.vh.value
                            }
                        },
                    )
                },
            )
        }
        // Associate the footer with the row that will get pushed off the bottom of the page if it can't fit.
        Footer(Modifier.fillMaxWidth().gridRow(2))
    }
}

object PageLayoutStyle {
    fun headerBackgroundColor(
        colorMode: ColorMode,
    ) = AppTheme(colorMode).colors.background.toRgb().copy(
        alpha = 204,
    )

    val headerFadeInKeyframes = Keyframes {
        from {
            Modifier
        }

        to {
            Modifier.background(
                color = headerBackgroundColor(colorMode = colorMode),
            )
        }
    }

    val headerFadeOutKeyframes = Keyframes {
        from {
            Modifier.background(
                color = headerBackgroundColor(colorMode = colorMode),
            )
        }

        to {
            Modifier
        }
    }
}
