package com.yamatrips.homepage.core.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.height
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.style.toModifier
import com.yamatrips.homepage.core.styles.AppTheme
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

@Composable
fun HeaderSection(
    title: String,
    subtitle: String? = null,
    modifier: Modifier = Modifier,
) {
    Box(
        modifier = modifier.fillMaxWidth()
            .height(size = 30.percent),
    ) {
        Column(
            verticalArrangement = Arrangement.spacedBy(space = 2.px),
            modifier = Modifier.align(alignment = Alignment.Center),
        ) {
            if (subtitle != null) {
                SpanText(
                    text = subtitle,
                    modifier = AppTheme.typography.body1.toModifier(),
                )
            }
            SpanText(
                text = title,
                modifier = AppTheme.typography.header1.toModifier(),
            )
        }
    }
}
