package com.yamatrips.homepage.core.ext

import com.varabyte.kobweb.browser.dom.observers.IntersectionObserver
import com.varabyte.kobweb.compose.dom.disposableRef
import org.w3c.dom.Element

fun <TElement : Element> intersectionObserver(
    thresholds: List<Double> = listOf(0.3),
    onIntersect: () -> Unit,
) = disposableRef<TElement> { element ->
    val observer = IntersectionObserver(
        options = IntersectionObserver.Options(root = null, thresholds = thresholds),
    ) { entries ->
        entries.firstOrNull()?.let { entry ->
            if (entry.isIntersecting) onIntersect()
        }
    }
    observer.observe(element)
    onDispose {
        observer.unobserve(element)
        observer.disconnect()
    }
}
