import com.varabyte.kobweb.silk.style.animation.toAnimation
import com.varabyte.kobweb.silk.style.toModifier
import com.yamatrips.homepage.core.ext.padding
import com.yamatrips.homepage.core.styles.AppTheme
import org.jetbrains.compose.web.css.AnimationTimingFunction
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

@Composable
fun HeroSection(
    title: String,
    subtitle: String? = null,
    description: String? = null,
    backgroundImageSrc: String,
    modifier: Modifier = Modifier,
) {
    val animatedAlpha = remember { mutableStateOf(0) }
    val animatedAlphaModifier = Modifier
        .onAnimationStart {
            animatedAlpha.value = 0
        }
        .animation(
            animation = HeroStyles.fadeInKeyframes.toAnimation(
                duration = 1000.ms,
                timingFunction = AnimationTimingFunction.EaseIn,
            ),
        )
        .onAnimationEnd {
            animatedAlpha.value = 1
        }
        .opacity(animatedAlpha.value)

    val slideUpModifier = Modifier
        .onAnimationStart {
            animatedAlpha.value = 0
        }
        .animation(
            HeroStyles.slideUpKeyframes.toAnimation(
                duration = 800.ms,
                delay = 200.ms,
                timingFunction = AnimationTimingFunction.EaseOut,
            ),
        )
        .onAnimationEnd {
            animatedAlpha.value = 1
        }
        .opacity(animatedAlpha.value)

    Box(
        modifier = modifier
            .fillMaxWidth()
            .backgroundColor(color = AppTheme.colors.background)
            // .height(100.vh),
    ) {
        Box(
            modifier = Modifier.fillMaxWidth(),
        ) {
            Image(
                src = backgroundImageSrc,
                modifier = Modifier
                    .fillMaxWidth()
                    .padding(bottom = 10.px)
                    .then(other = animatedAlphaModifier),
            )
            Image(
                src = "/overlay_trees.svg",
                modifier = Modifier
                    .zIndex(100)
                    .fillMaxWidth()
                    .align(alignment = Alignment.BottomCenter),
            )
            Column(
                verticalArrangement = Arrangement.spacedBy(space = 12.px),
                horizontalAlignment = Alignment.CenterHorizontally,
                modifier = Modifier
                    .zIndex(200)
                    .padding(vertical = 15.percent)
                    .align(alignment = Alignment.TopCenter)
                    .then(other = slideUpModifier),
            ) {
                if (subtitle != null) {
                    SpanText(
                        text = subtitle,
                        modifier = AppTheme.typography.header2.toModifier()
                            .textAlign(textAlign = TextAlign.Center)
                            .whiteSpace(whiteSpace = WhiteSpace.PreLine),
                    )
                }

                SpanText(
                    text = title,
                    modifier = AppTheme.typography.header1.toModifier()
                        .textAlign(textAlign = TextAlign.Center),
                )

                if (description != null) {
                    SpanText(
                        text = description,
                        modifier = AppTheme.typography.title2.toModifier()
                            .padding(top = 24.px)
                            .textAlign(textAlign = TextAlign.Center)
                            .whiteSpace(whiteSpace = WhiteSpace.PreLine),
                    )
                }
            }
        }
    }
}

object HeroStyles {
    val fadeInKeyframes = Keyframes {
        from {
            Modifier.opacity(0)
        }
        to {
            Modifier.opacity(1)
        }
    }

    val slideUpKeyframes = Keyframes {
        from {
            Modifier
                .opacity(0)
                .translateY(ty = (30).percent)
        }
        to {
            Modifier
                .opacity(1)
        }
    }
}
