package com.yamatrips.homepage.core.styles

import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.borderRadius
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.modifiers.setVariable
import com.varabyte.kobweb.silk.components.forms.ButtonStyle
import com.varabyte.kobweb.silk.components.forms.ButtonVars
import com.varabyte.kobweb.silk.style.addVariantBase
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

object AppVariants {
    val button = ButtonVariant
}

object ButtonVariant {
    val circle = ButtonStyle.addVariantBase {
        Modifier
            .padding(all = 0.px)
            .borderRadius(r = 50.percent)
    }

    val uncolored = ButtonStyle.addVariantBase {
        Modifier.setVariable(
            variable = ButtonVars.BackgroundDefaultColor,
            value = Colors.Transparent,
        )
    }
}
