            title = "VISION",
            subtitle = "ビジョン",
            header = " 日本をもう一度、誇れる国へ。",
            body = "日本の魅力を世界に発信することを通して、観光をきっかけに地域経済に新たな循環を生み出し、文化や暮らしの価値を再発見していきます。\n" +
