@Composable
fun Footer(modifier: Modifier = Modifier) {
    Row(
        horizontalArrangement = Arrangement.SpaceBetween,
        modifier = modifier
            .backgroundColor(color = AppTheme.colors.background)
            .padding(vertical = 3.cssRem, horizontal = 10.percent)
            .fillMaxWidth(),
    ) {
        Column(
            verticalArrangement = Arrangement.spacedBy(space = 1.cssRem),
        ) {
            Image(
                src = "/logo.svg",
                description = "Yamatrips Logo",
                modifier = Modifier
                    .display(value = DisplayStyle.Block),
            )
            SpanText(
                text = "info@yamatrips.com",
                modifier = AppTypography.footer1.toModifier()
                    .whiteSpace(WhiteSpace.PreLine),
            )
            Link(
                path = "/notice",
                text = "特定商取引法に基づく表記",
                variant = UncoloredLinkVariant,
                modifier = AppTypography.footer1.toModifier(),
            )
        }
        Column(
            verticalArrangement = Arrangement.spacedBy(space = 0.5.cssRem),
            modifier = Modifier.padding(horizontal = 2.cssRem),
        ) {
            MenuItems.toOrderedList().forEachIndexed { index, item ->
                Link(
                    path = item.path,
                    text = item.text,
                    variant = UncoloredLinkVariant,
                    modifier = AppTypography.footer1.toModifier(),
                )
            }
        }
        Row(
            horizontalArrangement = Arrangement.spacedBy(space = 0.5.cssRem),
        ) {
            val iconModifier = Modifier
                .size(size = 1.2.cssRem)
                .display(value = DisplayStyle.Block)

            Link(path = "/") {
                Image(
                    src = "/ic_instagram.svg",
                    description = "Instagram",
                    modifier = iconModifier,
                )
            }
            Link(path = "/") {
                Image(
                    src = "/ic_youtube.svg",
                    description = "Youtube",
                    modifier = iconModifier,
                )
            }
            Link(path = "/") {
                Image(
                    src = "/ic_rexby.svg",
                    description = "Rexby",
                    modifier = iconModifier,
                )
            }
        }
    }
    // Box(FooterStyle.toModifier().then(modifier), contentAlignment = Alignment.Center) {
