package com.yamatrips.homepage.core.ext

import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.silk.style.StyleScope
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint

fun StyleScope.from(breakpoint: Breakpoint, createModifier: () -> Modifier) {
    (breakpoint..Breakpoint.XL).invoke(createModifier)
}

fun StyleScope.sm(createModifier: () -> Modifier) {
    from(breakpoint = Breakpoint.SM, createModifier = createModifier)
}

fun StyleScope.md(createModifier: () -> Modifier) {
    from(breakpoint = Breakpoint.MD, createModifier = createModifier)
}

fun StyleScope.lg(createModifier: () -> Modifier) {
    from(breakpoint = Breakpoint.LG, createModifier = createModifier)
}

