enum class MenuItems(val path: String, val text: String) {
    About(path = "#about", text = "About"),
    Services(path = "#services", text = "Services"),
    Works(path = "#works", text = "Works"),
    Contact(path = "https://forms.gle/YpoeaJCg4zi4yCj16", text = "Contact"),
    ;

    companion object {
        fun toOrderedList() = listOf(
            About,
            Services,
            Works,
            Contact,
        )
    }
}

object NavHeaderStyles {
    val container = CssStyle.base {
        Modifier
            .fillMaxWidth()
            .padding(topBottom = 1.cssRem, leftRight = 2.cssRem)
    }

    val sideMenuAnimation = Keyframes {
        from {
            Modifier.translateX(100.percent)
        }

        to {
            Modifier
        }
    }
}

@Composable
fun NavHeader(
    modifier: Modifier = Modifier,
    ref: ElementRefScope<HTMLElement>? = null,
) {
    Row(
        verticalAlignment = Alignment.CenterVertically,
        modifier = modifier
            .zIndex(5000)
            .then(other = NavHeaderStyles.container.toModifier()),
        ref = ref,
    ) {
        Link(path = "/") {
            Image(
                src = "/logo.svg",
                description = "Yamatrips Logo",
                modifier = Modifier
                    .height(size = 1.cssRem)
                    .display(value = DisplayStyle.Block),
            )
        }

        Spacer()

        Row(
            verticalAlignment = Alignment.CenterVertically,
            modifier = Modifier
                .gap(value = 1.5.cssRem)
                .displayIfAtLeast(Breakpoint.MD),
        ) {
            MenuItems.toOrderedList().forEach {
                Link(
                    path = it.path,
                    text = it.text,
                    variant = UncoloredLinkVariant,
                    modifier = AppTypography.nav1.toModifier(),
                )
            }
        }

        Row(
            verticalAlignment = Alignment.CenterVertically,
            modifier = Modifier
                .fontSize(value = 1.5.cssRem)
                .gap(value = 1.cssRem)
                .displayUntil(Breakpoint.MD),
        ) {
            var menuState by remember { mutableStateOf(SideMenuState.CLOSED) }

            IconButton(onClick = { menuState = SideMenuState.OPEN }) {
                HamburgerIcon()
            }

            if (menuState != SideMenuState.CLOSED) {
                SideMenu(
                    menuState = menuState,
                    onClickClose = { menuState = menuState.close() },
                    onAnimationEnd = { if (menuState == SideMenuState.CLOSING) menuState = SideMenuState.CLOSED },
                    modifier = Modifier
                        .zIndex(8000)
                )
            }
        }
    }
}

