package com.yamatrips.homepage.core.sections

import androidx.compose.runtime.Composable
import androidx.compose.runtime.key
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.animation
import com.varabyte.kobweb.compose.ui.modifiers.backgroundColor
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxSize
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.gap
import com.varabyte.kobweb.compose.ui.modifiers.onAnimationEnd
import com.varabyte.kobweb.compose.ui.modifiers.onClick
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.modifiers.setVariable
import com.varabyte.kobweb.silk.components.icons.CloseIcon
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.navigation.UncoloredLinkVariant
import com.varabyte.kobweb.silk.components.overlay.Overlay
import com.varabyte.kobweb.silk.components.overlay.OverlayVars
import com.varabyte.kobweb.silk.style.animation.toAnimation
import com.varabyte.kobweb.silk.style.toModifier
import com.yamatrips.homepage.core.components.IconButton
import com.yamatrips.homepage.core.styles.AppTheme
import com.yamatrips.homepage.core.styles.AppTypography
import org.jetbrains.compose.web.css.AnimationDirection
import org.jetbrains.compose.web.css.AnimationFillMode
import org.jetbrains.compose.web.css.AnimationTimingFunction
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.ms

enum class SideMenuState {
    CLOSED,
    OPEN,
    CLOSING;

    fun close() = when (this) {
        CLOSED -> CLOSED
        OPEN -> CLOSING
        CLOSING -> CLOSING
    }
}

@Composable
internal fun SideMenu(
    menuState: SideMenuState,
    onClickClose: () -> Unit,
    onAnimationEnd: () -> Unit,
    modifier: Modifier = Modifier,
) {
    Overlay(
        modifier
            .setVariable(variable = OverlayVars.BackgroundColor, value = Colors.Transparent)
            .onClick { onClickClose() },
    ) {
        key(menuState) {
            Column(
                horizontalAlignment = Alignment.End,
                modifier = Modifier
                    .fillMaxSize()
                    .padding(top = 1.cssRem, leftRight = 1.cssRem)
                    .gap(1.5.cssRem)
                    .backgroundColor(color = AppTheme.colors.background.toRgb().copy(alpha = 204))
                    .animation(
                        NavHeaderStyles.sideMenuAnimation.toAnimation(
                            duration = 200.ms,
                            timingFunction = if (menuState == SideMenuState.OPEN) AnimationTimingFunction.EaseOut else AnimationTimingFunction.EaseIn,
                            direction = if (menuState == SideMenuState.OPEN) AnimationDirection.Normal else AnimationDirection.Reverse,
                            fillMode = AnimationFillMode.Forwards,
                        ),
                    )
                    .onAnimationEnd { onAnimationEnd() },
            ) {
                IconButton(onClick = onClickClose) {
                    CloseIcon()
                }
                Column(
                    horizontalAlignment = Alignment.End,
                    modifier = Modifier
                        .padding(right = 0.75.cssRem)
                        .gap(value = 1.5.cssRem)
                        .fontSize(value = 1.4.cssRem),
                ) {
                    MenuItems.toOrderedList().forEach {
                        Link(
                            path = it.path,
                            text = it.text,
                            variant = UncoloredLinkVariant,
                            modifier = AppTypography.nav1.toModifier(),
                        )
                    }
                }
            }
        }
    }
}
