package com.yamatrips.homepage.core.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.RowScope
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.setVariable
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.forms.ButtonVars
import com.yamatrips.homepage.core.styles.AppTheme
import org.jetbrains.compose.web.css.em

@Composable
fun IconButton(
    onClick: () -> Unit,
    content: @Composable RowScope.() -> Unit,
) {
    Button(
        onClick = { onClick() },
        Modifier.setVariable(
            variable = ButtonVars.FontSize,
            value = 1.em,
        ), // Make button icon size relative to parent container font size
        variant = AppTheme.variants.button.circle.then(next = AppTheme.variants.button.uncolored),
        content = content,
    )
}
