package com.yamatrips.homepage.company.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.css.WhiteSpace
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.gap
import com.varabyte.kobweb.compose.ui.modifiers.grid
import com.varabyte.kobweb.compose.ui.modifiers.gridItem
import com.varabyte.kobweb.compose.ui.modifiers.textAlign
import com.varabyte.kobweb.compose.ui.modifiers.whiteSpace
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.style.toModifier
import com.yamatrips.homepage.core.entities.CompanyInfo
import com.yamatrips.homepage.core.ext.padding
import com.yamatrips.homepage.core.styles.AppTheme
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Div

@Composable
fun CompanySection(
    title: String,
    companyInfoList: List<CompanyInfo>,
    modifier: Modifier = Modifier,
) = Column(
    horizontalAlignment = Alignment.CenterHorizontally,
    modifier = modifier.fillMaxWidth(),
) {
    SpanText(
        text = title,
        modifier = AppTheme.typography.header3.toModifier()
            .textAlign(textAlign = TextAlign.Center)
            .padding(horizontal = 10.percent, vertical = 40.px),
    )
    Div(
        attrs = modifier
            .gap(40.px)
            .grid {
                auto {}
            }
            .toAttrs(),
    ) {
        companyInfoList.forEachIndexed { index, item ->
            CompanyItem(index = index, companyInfo = item)
        }
    }
}

@Composable
fun CompanyItem(
    index: Int,
    companyInfo: CompanyInfo,
) {
    SpanText(
        text = companyInfo.title,
        modifier = AppTheme.typography.title2.toModifier()
            .gridItem(row = index + 1, column = 1),
    )
    SpanText(
        text = companyInfo.description,
        modifier = AppTheme.typography.body1.toModifier()
            .whiteSpace(whiteSpace = WhiteSpace.PreLine)
            .gridItem(row = index + 1, column = 2),
    )
}
