import androidx.compose.runtime.Composable
import androidx.compose.runtime.ProvidableCompositionLocal
import androidx.compose.runtime.compositionLocalOf
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlin.Any
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable
import com.yamatrips.homepage.contact.pages.ContactPage as _com_yamatrips_homepage_contact_pages_ContactPage
import com.yamatrips.homepage.core.layouts.MarkdownLayout as _com_yamatrips_homepage_core_layouts_MarkdownLayout
import com.yamatrips.homepage.core.layouts.PageLayout as _com_yamatrips_homepage_core_layouts_PageLayout
import com.yamatrips.homepage.home.pages.HomePage as _com_yamatrips_homepage_home_pages_HomePage
import com.yamatrips.homepage.notice.pages.NoticePage as _com_yamatrips_homepage_notice_pages_NoticePage
import com.yamatrips.homepage.pages.AboutMdPage as _com_yamatrips_homepage_pages_AboutMdPage

private val LayoutScopeLocal: ProvidableCompositionLocal<Any?> = compositionLocalOf<Any?> { null }

@Composable
private fun <T : Any> currentLayoutScope(): T {
    @Suppress("UNCHECKED_CAST")
    return LayoutScopeLocal.current as? T
        ?: error("Unexpected between Page/Layout scope and the parent Layout which it is providing it. Please report this issue to the Kobweb developers.")
}

@Composable
private fun <T : Any> provideLayoutScope(layoutScope: T, content: @Composable () -> Unit) {
    androidx.compose.runtime.CompositionLocalProvider(LayoutScopeLocal provides layoutScope, content = content)
}

public fun main() {
    AppGlobals.initialize(mapOf("title" to "YamaTrips"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.registerLayout(
            "com.yamatrips.homepage.core.layouts.MarkdownLayout",
            parentLayoutId = "com.yamatrips.homepage.core.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.yamatrips.homepage.core.layouts.initMarkdownLayout(ctx)
            },
        ) { pageCtx, pageMethod -> 
            _com_yamatrips_homepage_core_layouts_MarkdownLayout {
                pageMethod(pageCtx)
            }
        }
        ctx.router.registerLayout(
            "com.yamatrips.homepage.core.layouts.PageLayout",
        ) { pageCtx, pageMethod -> 
            _com_yamatrips_homepage_core_layouts_PageLayout(pageCtx) {
                provideLayoutScope(this) {
                    pageMethod(pageCtx)
                }
            }
        }
        ctx.router.register(
            "/",
            layoutId = "com.yamatrips.homepage.core.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.yamatrips.homepage.home.pages.initHomePage(ctx)
            },
        ) { pageCtx -> 
            _com_yamatrips_homepage_home_pages_HomePage()
        }
        ctx.router.register(
            "/about-md",
            layoutId = "com.yamatrips.homepage.core.layouts.MarkdownLayout",
            initRouteMethod = { ctx ->
                com.yamatrips.homepage.pages.initAboutMdPage(ctx)
            },
        ) { pageCtx -> 
            _com_yamatrips_homepage_pages_AboutMdPage()
        }
        ctx.router.register(
            "/contact",
            layoutId = "com.yamatrips.homepage.core.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.yamatrips.homepage.contact.pages.initContactPage(ctx)
            },
        ) { pageCtx -> 
            _com_yamatrips_homepage_contact_pages_ContactPage()
        }
        ctx.router.register(
            "/notice",
            layoutId = "com.yamatrips.homepage.core.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.yamatrips.homepage.notice.pages.initContactPage(ctx)
            },
        ) { pageCtx -> 
            _com_yamatrips_homepage_notice_pages_NoticePage()
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("nav-header-styles_container", com.yamatrips.homepage.core.sections.NavHeaderStyles.container)
        ctx.theme.registerStyle("app-typography_body1", com.yamatrips.homepage.core.styles.AppTypography.body1)
        ctx.theme.registerStyle("app-typography_body2", com.yamatrips.homepage.core.styles.AppTypography.body2)
        ctx.theme.registerStyle("app-typography_header1", com.yamatrips.homepage.core.styles.AppTypography.header1)
        ctx.theme.registerStyle("app-typography_header2", com.yamatrips.homepage.core.styles.AppTypography.header2)
        ctx.theme.registerStyle("app-typography_header3", com.yamatrips.homepage.core.styles.AppTypography.header3)
        ctx.theme.registerStyle("app-typography_nav1", com.yamatrips.homepage.core.styles.AppTypography.nav1)
        ctx.theme.registerStyle("app-typography_footer1", com.yamatrips.homepage.core.styles.AppTypography.footer1)
        ctx.theme.registerStyle("app-typography_title1", com.yamatrips.homepage.core.styles.AppTypography.title1)
        ctx.theme.registerStyle("app-typography_title2", com.yamatrips.homepage.core.styles.AppTypography.title2)
        ctx.theme.registerStyle("markdown", com.yamatrips.homepage.core.layouts.MarkdownStyle)
        ctx.theme.registerStyle("works-styles_grid", com.yamatrips.homepage.works.pages.WorksStyles.GridStyle)
        ctx.theme.registerVariant("-circle", com.yamatrips.homepage.core.styles.ButtonVariant.circle
        )
        ctx.theme.registerVariant("-uncolored", com.yamatrips.homepage.core.styles.ButtonVariant.uncolored
        )
        ctx.theme.registerKeyframes("nav-header-styles_side-menu", com.yamatrips.homepage.core.sections.NavHeaderStyles.sideMenuAnimation)
        ctx.theme.registerKeyframes("content-styles_slide-in-from-left", com.yamatrips.homepage.core.components.ContentStyles.slideInFromLeftKeyframes)
        ctx.theme.registerKeyframes("content-styles_slide-in-from-right", com.yamatrips.homepage.core.components.ContentStyles.slideInFromRightKeyframes)
        ctx.theme.registerKeyframes("works-item-styles_slide-in-from-bottom", com.yamatrips.homepage.core.components.WorksItemStyles.slideInFromBottom)
        ctx.theme.registerKeyframes("page-layout-style_header-fade-in", com.yamatrips.homepage.core.layouts.PageLayoutStyle.headerFadeInKeyframes)
        ctx.theme.registerKeyframes("page-layout-style_header-fade-out", com.yamatrips.homepage.core.layouts.PageLayoutStyle.headerFadeOutKeyframes)
        ctx.theme.registerKeyframes("hero-styles_fade-in", com.yamatrips.homepage.home.components.HeroStyles.fadeInKeyframes)
        ctx.theme.registerKeyframes("hero-styles_slide-up", com.yamatrips.homepage.home.components.HeroStyles.slideUpKeyframes)
        ctx.theme.registerKeyframes("service-item-styles_slide-in-from-left", com.yamatrips.homepage.services.components.ServiceItemStyles.slideInFromLeftKeyframes)
        ctx.theme.registerKeyframes("service-item-styles_slide-in-from-right", com.yamatrips.homepage.services.components.ServiceItemStyles.slideInFromRightKeyframes)
        ctx.theme.registerKeyframes("message-styles_slide-in-from-bottom", com.yamatrips.homepage.company.components.MessageStyles.slideInFromBottomKeyframes)
        com.yamatrips.homepage.initColorMode(ctx)
        com.yamatrips.homepage.initStyles(ctx)
        com.yamatrips.homepage.core.styles.initTheme(ctx)
        com.yamatrips.homepage.core.styles.initSiteStyles(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        com.yamatrips.homepage.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
