@InitRoute
fun initHomePage(ctx: InitRouteContext) {
    ctx.data.add(PageLayoutData("Home"))
}

@OptIn(ExperimentalUuidApi::class)
@Page
@Layout("com.yamatrips.homepage.core.layouts.PageLayout")
@Composable
fun HomePage() {
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier.fillMaxWidth(),
    ) {
        HeroSection(
            title = "Local Japan, Global Reach.",
            subtitle = "世界に刺さる、地方ブランディング",
            description = "「世界は、日本のどこに惹かれているのか？」YamaTripsは、地域や企業と共にその答えを探ります。\n" +
                "埋もれた魅力を発掘し、体験として世界へ届ける。観光を超えた出会いが、地域に新しい価値と活力をもたらします。",
            backgroundImageSrc = "/bg_home.png",
            modifier = Modifier.fillMaxWidth(),
        )
        AboutPage()
        ServicesPage()
        WorksPage()
        CompanyPage()
    }
}
